import {Validate} from './validate.js'

export class UserValidate extends Validate {
  _rule = {
    account: 'require|phone',
    password: 'require'
  }
  _message = {
    'account.require': '',
    'account.phone': '',
    'password.require': ''
  }
  _scene = {
    login: ['account', 'password']
  }
}



